.bigcon{
  background-color:white;
  width:100vw;
  height:100vh;
  display:flex;
justify-content: center;
align-items: center;
}
span{
  color:hsl(44, 100%, 49%)
}
.logo{
  color:black;
  margin-right: 40px;

}
.form{
  border-left: 6px solid hsl(44, 100%, 49%);
  background-color:white;
  padding:30px;
  width:800px;
  height:auto;
  display:flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 19px;
  align-items: center;
  box-shadow: 0px 7px 50px -3px hsl(44, 59%, 92%);
}
.input{
  width:600px;
  height:60px;
  margin-top: 20px;
  outline: none;
  border:none;
  border-bottom:0.5px solid hsl(180, 96%, 42%);
  border-radius: 8px;
  font-size: large;
}
input[type=text]:focus{
  border-bottom:3px solid hsl(44, 100%, 49%)
}
.submit{
  color:black;
  width:100px;
  height:40px;
  background-color: hsl(44, 100%, 49%);
  cursor: pointer;
  outline: none;
  border:none;
  border-radius: 8px;
  margin-top: 20px;
}
