.con{
    width:800px;
    height:800px;

    margin-left: 500px;
    display:flex;
    justify-content: center;
    flex-direction: row;
  
}
.concard{
    width:300px;
    height:150px;
    border-left: 5px solid hsl(44, 100%, 49%);
    background-color: white;
    padding:20px;
    margin-top: 20px;
    box-shadow: -9px 7px 100px 4px rgba(0,0,0,0.1);
    border-radius: 8px;



}
.modal{
    width:700px;
    height:150px;
   
    position: absolute;
    left: 30%;
    top:20%;
    box-shadow: -9px 7px 100px 4px rgba(0,0,0,0.1);
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    flex-direction: column;
}
.modalcon{
    width:100vw;
    height:100vh;
    box-shadow: -9px 7px 100px 4px rgba(0,0,0,0.1);
    position: fixed;
    background-color: rgb(0,0,0); 
  background-color: rgba(0,0,0, 0.4);
}
.button{
    padding:10px 10px;
    margin-top:20px;
    background-color: hsl(44, 100%, 49%);
    border: none;
 cursor: pointer;
    border-radius: 4px;
}
.button:hover{
    background-color: black;
    color:white;
}

.price{
    color:rgb(148, 146, 146);
    font-weight: 800;
}
.buyinput{
    width:400px;
    height:35px;
    margin-right: 10px;

}
.buysubmit{
    width:100px;
    height:40px;
    background-color: hsl(44, 100%, 49%);
border-radius: 8px;
    border:none;
    cursor: pointer;
}
.buysubmit:hover{
    background-color:black;
    color:white
}
.text{
    margin-bottom: -20px;
    margin-left: -70px;
    color:rgb(1, 58, 41);

}
.close{
    width:20px;
    height: 20px;
    background-color: red;
    border-radius: 50%;
    position: absolute;
    top:-5px;
    right: -5px;
    box-shadow: -32px 28px 26px -10px rgba(0,0,0,0.1);
    cursor: pointer;
}